.tab-controls {
  display: flex;
  flex-wrap: wrap;
  max-width: 902px;
  width: 100%;
  border-bottom: 1px solid rgba(44, 46, 63, 0.3);
  margin-bottom: 80px;
}

.tab-controls__item {
  border: none;
  background-color: transparent;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: -0.415px;
  color: rgba(44, 46, 63, 0.3);
  margin-right: 99px;
  padding: 15px 0;

  &:hover,
  &:focus {
    color: #fe7865;
  }

  &:active {
    opacity: 0.5;
  }
}

.tab-controls__item:last-child {
  margin-right: 0;
}

@media (max-width: $tablet-width-only) {
  .tab-controls {
    flex-wrap: nowrap;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 33px;
    overflow-y: scroll;
  }
}
